@import url("https://fonts.googleapis.com/css2?family=Chivo:wght@300;400;700&display=swap");
/*
  0 - 600px:       Phone
  600 - 900px:     Tablet portrait
  900 - 1200px:    Tablet landscape
  [1200 - 1800] is where our normal styles apply. > Desktop frst aprouch
  1800px + :       Big desktop
*/
/*
 Example:

h1 {
  font-size: rem(33.536);
}

// In CSS:
h1 {
  font-size: 3.3536rem;
}

*/
*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
}
@media (max-width: ) {
  html {
    font-size: 68.7%;
  }
}
@media (min-width: 112.5em) {
  html {
    font-size: 68.7%;
  }
}
@media (max-width: 89em) {
  html {
    font-size: 56.25%;
  }
}
@media (max-width: 75em) {
  html {
    font-size: 50%;
  }
}

body {
  box-sizing: border-box;
  font-family: "Chivo", sans-serif;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

:root {
  --primary-color: #54E6AF;
  --secondary-color: #121725;
  --primary-light: #b3ffe2;
  --secondary-col-light: #2C344B;
  --secondary-col-light2: #5A668A;
  --gray-color: #C2CBE5;
  --gray-color2: #c2cce5;
  --white: #fff;
  --error-color: #fb3c3c;
  --success-color: #54E6AF;
}

.section-hero {
  height: 100vh;
  padding: 13rem 0 13rem 16.5rem;
  background-color: var(--secondary-color);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
}
@media (max-width: 89em) {
  .section-hero {
    padding: 12rem 0 12rem 15.5rem;
  }
}
@media (max-width: 59rem) {
  .section-hero {
    padding: 0 0 0 3.9rem;
    justify-content: start;
    align-items: start;
  }
}
@media (max-width: 37.5em) {
  .section-hero {
    padding: 0;
  }
}
.section-hero__logo-hero {
  width: 13.5rem;
  height: 5.6rem;
  position: absolute;
  top: 10.2rem;
  left: 16.5rem;
}
@media (max-width: ) {
  .section-hero__logo-hero {
    top: 10rem;
    left: 19.2rem;
  }
}
@media (min-width: 112.5em) {
  .section-hero__logo-hero {
    top: 10rem;
    left: 19.2rem;
  }
}
@media (max-width: 59rem) {
  .section-hero__logo-hero {
    top: 5rem;
    left: 3.9rem;
  }
}
@media (max-width: 37.5em) {
  .section-hero__logo-hero {
    top: 6.2rem;
    left: 35%;
    width: 16.75rem;
    height: 7rem;
  }
}
@media (max-width: 23.4375em) {
  .section-hero__logo-hero {
    left: 30%;
  }
}
.section-hero__dots {
  position: absolute;
  right: 0;
  bottom: 9.4rem;
}
@media (max-width: ) {
  .section-hero__dots {
    bottom: 7.5rem;
  }
}
@media (min-width: 112.5em) {
  .section-hero__dots {
    bottom: 7.5rem;
  }
}
@media (max-width: 89em) {
  .section-hero__dots {
    bottom: 13.5rem;
  }
}
@media (max-width: 75em) {
  .section-hero__dots {
    bottom: 18.9rem;
  }
}
@media (max-width: 59rem) {
  .section-hero__dots {
    bottom: 0;
    left: 3rem;
  }
}
@media (max-width: 37.5em) {
  .section-hero__dots {
    display: none;
  }
}

.hero-container {
  padding-top: 10.3rem;
  background-image: url(/assets/desktop/image-host.jpg);
  background-repeat: no-repeat;
  background-size: 69.65%;
  background-position: 100% top;
}
@media (max-width: 59rem) {
  .hero-container {
    padding-top: 25.8rem;
    background-image: url(/assets/tablet/image-host.jpg);
  }
}
@media (max-width: 37.5em) {
  .hero-container {
    background-size: 100%;
    padding-top: 0rem;
    background-image: linear-gradient(rgba(18, 23, 37, 0.8), rgba(18, 23, 37, 0.8)), url(/assets/mobile/image-host.jpg);
  }
}

.hero-container__info {
  max-width: 56.71%;
  padding: 8.8rem 5.8rem 0 0;
  background-color: var(--secondary-color);
}
@media (max-width: 89em) {
  .hero-container__info {
    padding: 8.8rem 4.5rem 0 0;
  }
}
@media (max-width: 75em) {
  .hero-container__info {
    padding: 8.8rem 0 0 0;
  }
}
@media (max-width: 59rem) {
  .hero-container__info {
    padding: 9.3rem 0 0 0;
    max-width: 65%;
  }
}
@media (max-width: 48em) {
  .hero-container__info {
    padding: 9.3rem 0 0 0;
    max-width: 75%;
  }
}
@media (max-width: 37.5em) {
  .hero-container__info {
    max-width: 100%;
    height: 100vh;
    background-color: transparent;
    padding: 21.9rem 0 0 0;
    text-align: center;
  }
}
@media (max-width: 23.4375em) {
  .hero-container__info {
    padding: 21.9rem 3rem 0 3rem;
    display: grid;
    grid-template-columns: 1fr;
  }
}
.hero-container__info__heading {
  font-size: 5.2rem;
  font-weight: 300;
  line-height: 6.2rem;
  text-transform: uppercase;
  color: var(--primary-color);
  margin-bottom: 2.4rem;
}
.hero-container__info__heading span {
  color: var(--white);
}
@media (max-width: 37.5em) {
  .hero-container__info__heading {
    margin-bottom: 2rem;
  }
}
@media (max-width: 23.4375em) {
  .hero-container__info__heading {
    font-size: 3.25rem;
    line-height: 4.75rem;
    text-align: center;
    margin: 0 -1rem 2rem -1rem;
  }
}
.hero-container__info__description {
  font-size: 1.8rem;
  line-height: 2.8rem;
  color: var(--gray-color2);
  font-weight: 300;
  margin-bottom: 4rem;
  margin-right: 22rem;
}
@media (max-width: 48em) {
  .hero-container__info__description {
    margin-right: 24rem;
  }
}
@media (max-width: 37.5em) {
  .hero-container__info__description {
    margin: 0 10rem 4.125rem;
    font-size: 1.875rem;
  }
}
@media (max-width: 23.4375em) {
  .hero-container__info__description {
    line-height: 3.125rem;
    margin: 0;
  }
}

.hero-container__form {
  max-width: 42.7rem;
  position: relative;
  margin-bottom: 6.4rem;
}
@media (max-width: 37.5em) {
  .hero-container__form {
    max-width: 70%;
    margin: 0 auto 7.5rem auto;
  }
}
@media (max-width: 23.4375em) {
  .hero-container__form {
    max-width: 100%;
    grid-row: 4/5;
  }
}
.hero-container__form__input {
  width: 100%;
  padding: 1.4rem 3.2rem;
  border: none;
  border-radius: 5rem;
  font-family: inherit;
  font-size: 1.4rem;
  line-height: 2.8rem;
  font-weight: 700;
  color: var(--white);
  background-color: var(--secondary-col-light);
}
.hero-container__form__input::placeholder {
  color: var(--white);
  font-weight: 700;
}
@media (max-width: 23.4375em) {
  .hero-container__form__input {
    font-size: 1.75rem;
    margin-bottom: 2rem;
  }
}
.hero-container__form__btn {
  position: absolute;
  top: 0.5rem;
  right: 0.5rem;
  padding: 0.9rem 2.7rem;
  border-radius: 5rem;
  cursor: pointer;
  border: none;
  font-family: inherit;
  font-size: 1.4rem;
  line-height: 2.8rem;
  font-weight: 900;
  background-color: var(--primary-color);
  color: var(--secondary-color);
  transition: all 300ms ease-out;
}
.hero-container__form__btn:hover {
  background-color: var(--primary-light);
}
@media (max-width: 23.4375em) {
  .hero-container__form__btn {
    position: relative;
    font-size: 1.75rem;
    line-height: 3.5rem;
    width: 100%;
  }
}
.hero-container__logos {
  display: flex;
  gap: 4rem;
}
@media (max-width: 37.5em) {
  .hero-container__logos {
    width: 100%;
    justify-content: center;
  }
}
@media (max-width: 23.4375em) {
  .hero-container__logos {
    grid-row: 3/4;
    margin-bottom: 1rem;
  }
}
.hero-container__logo {
  opacity: 55%;
}
.hero-container__logo:last-of-type {
  margin-left: -1.3rem;
}
@media (max-width: 37.5em) {
  .hero-container__logo {
    width: 10rem;
  }
}
@media (max-width: 23.4375em) {
  .hero-container__logo {
    width: 7rem;
    height: 2.125rem;
  }
  .hero-container__logo:last-of-type {
    margin-left: -2rem;
  }
}

.hero-container__form__msg {
  color: var(--error-color);
  font-size: 1.2rem;
  line-height: 1.4rem;
  position: absolute;
  bottom: -2rem;
  left: 3.2rem;
  visibility: hidden;
}
.hero-container__form.success .hero-container__form__input {
  outline: 1px solid var(--success-color);
}
.hero-container__form.error .hero-container__form__input {
  outline: 1px solid var(--error-color);
}
.hero-container__form.error .hero-container__form__msg {
  visibility: visible;
}

*:focus {
  outline: none;
  box-shadow: 0 0 0 0.3rem rgba(179, 255, 226, 0.5);
}